 @import "../../assets/scss/Colors.module.scss";
@import "../../assets/scss/Fonts.module.scss";
 
 .button {
     
      color: $Background;
      background: $MainColor;
      border: none;
      height: 60px;
      font-weight: $Bold;
      text-align: center;
      border-radius: 15px;
      cursor: pointer;
      transition: all 0.4s ease;

      &:hover {
        background: $Background;
        color: $MainColor;
        border: 1px solid $MainColor;
      }

      &:active {
        background: $ActiveColor;
      }
    }