@import "../../../assets/scss/Colors.module.scss";
@import "../../../assets/scss/Fonts.module.scss";
@import "../../../assets/scss/Mixin.module.scss";

.page {
  .headline {
    background-color: $BackgroundOpacity;
    .container {
      @include container;
      min-height: 100vh;
      color: $MainColor;
      display: flex;
      flex-direction: column;

      .title {
        font-size: $fz46;
        text-align: center;
        padding-top: 3%;
      }

      h3 {
        font-size: $fz20;
        text-align: center;
        margin: 20px 0;
        color: $SubmitHover;
      }

      .levelWrapp {
        display: flex;
        padding: 3%;

        img {
          max-width: 400px;
          min-width: 100px;
          min-height: 80px;
        }

        .level {
          font-size: $fz28;
          text-align: center;
          padding-left: 10%;

          li {
            font-size: $fz24;
            display: flex;
            align-items: center;

            .icon {
              font-size: $fzH2;
              margin: 3%;
            }
          }
        }
      }
    }
  }

  .goals {
    background-color: $BackgroundOpacity;
    .container {
      @include container;
      padding: 3% 0;

      .right,
      .left {
        display: flex;
        align-items: center;
        justify-content: space-between;

        .image {
          max-width: 400px;
          // min-width: 100px;
        }

        h3 {
          margin: 0 3%;
          font-size: $fz24;
        }
      }

      .left {
        flex-direction: row-reverse;
      }
    }
  }

  .offer {
    background-color: $ActiveColor;
    //  background: url(../../../assets/images/learn2.jpg) no-repeat center center;
    // background-size: cover;

    .container {
      @include container;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: first baseline;
      max-height: 100%;

      h2 {
        color: $Shadow;
        font-size: $fz24;
        padding: 2% 0;
      }
      p {
        font-size: $fz28;
        color: $MainColor;
        font-weight: bold;
        text-align: center;
      }

      img {
        max-width: 350px;
        min-width: 100px;
        max-height: 350px;
        min-height: 100px;
        padding: 2% 0;
      }
    }
  }

  .prefer {
    .container {
      @include container;
      display: flex;
      align-items: center;

      .companies,
      .individual {
        display: flex;
        min-height: 400px;
        padding: 2% 0;

        h2 {
          text-align: left;
        }
        p {
          font-size: $fz20;
        }

        .iconGroup,
        .iconBook {
          min-width: 100px;
          min-height: 100px;
          font-size: $fz46;
          border: 4px solid $MainColor;
          border-radius: 50%;
          color: $MainColor;
          margin: 4%;
        }
      }
    }
  }
}

// Mobile
@media screen and (max-width: 520px) {
  .page {
    .headline {
      .container {
        padding: $PaddingMobile;
        .levelWrapp {
          flex-direction: column;
          align-items: center;

          img{
            max-width: 300px;
          }
        }
      }
    }

    .goals {
      .container {
        padding: $PaddingMobile;
        .right,
        .left {
          flex-direction: column;

          .image {
            max-width: 300px;
          }

          h3 {
            text-align: center;
          }
        }
      }
    }

    .offer {
      .container {
        padding: $PaddingMobile;

        img {
          max-width: 300px;
        }
      }
    }

    .prefer {
      .container {
        flex-direction: column;
        padding: $PaddingMobile;

        .companies,
        .individual {
          padding: 6% 0 0 0;

          h2 {
            padding: 0;
          }

          p {
            padding-top: 6%;
          }

          .iconGroup,
          .iconBook {
            min-width: 50px;
            min-height: 50px;
            font-size: $fz30;
          }
        }
      }
    }
  }
}

//  Tablet SD
@media screen and (min-width: 521px) and (max-width: 768px) {
  .page {
    .headline {
      .container {
        padding: $PaddingTablet;
        .levelWrapp {
          flex-direction: column;
          align-items: center;
        }
      }
    }

    .goals {
      .container {
        padding: $PaddingTablet;
        .right,
        .left {
          flex-direction: column;

          h3 {
            text-align: center;
            padding: 3% 0;
          }
        }
      }
    }

    .offer {
      .container {
        padding: $PaddingTablet;
      }
    }

    .prefer {
      .container {
        flex-direction: column;
        padding: $PaddingTablet;

        .companies,
        .individual {
          padding: 6% 0 0 0;

          h2 {
            padding: 0;
          }

          p {
            padding-top: 6%;
          }

          .iconGroup,
          .iconBook {
            min-width: 50px;
            min-height: 50px;
            font-size: $fz30;
          }
        }
      }
    }
  }
}

// Tablet HD
@media screen and (min-width: 769px) and (max-width: 1350px) {
  .page {
    .headline {
      .container {
        padding: $PaddingTablet;
      }
    }

    .goals {
      .container {
        padding: $PaddingTablet;
      }
    }

    .offer {
      .container {
        padding: $PaddingTablet;
      }
    }

    .prefer {
      .container {
        padding: $PaddingTablet;
      }
    }
  }
}
